@import url(https://fonts.googleapis.com/css2?family=Barlow&family=Montserrat&display=swap);
:root {
  --highlight: rgb(230, 57, 70);
  --light: rgb(241, 250, 238);
  --dark: rgb(29, 53, 87);
  --main-light: rgb(166, 232, 233);
  --main-dark: rgb(65, 123, 158);
}

html{
  font-size: 10px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: rgb(247, 247, 247);
  color: rgb(54, 54, 54);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h3{
  font-size: 2.7rem;
  color: rgb(166, 232, 233);
  color: var(--main-light);
}

p {
  font-size: 1.9rem;
  font-family: Barlow, sans-serif;
}

small {
  font-size: 1.5rem;
  font-family: Montserrat, sans-serif;
}

a, a:hover, a:focus, a:active, a:visited, a:link {
  text-decoration: none;
}

.container {
  margin: 0 auto;
  max-width: 1000px;
  padding: 40px;
}

.title {
  font-size: 4.4rem;
  color: rgb(230, 57, 70);
  color: var(--highlight);
  text-align: left;
}

.tagline{
  color: rgb(65, 123, 158);
  color: var(--main-dark);
  margin-left: 1rem;
}

.score{
  background-color: rgb(29, 53, 87);
  background-color: var(--dark);
  font-family: Montserrat;
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
  border-radius: 5px;
  padding: .4rem .8rem;
  text-align: center;
  text-justify: inter-word;
  margin-left: 1rem;
}

.button1 {
  display: inline-block;
  border: none;
  border-radius: 20px;
  padding: .9rem 1.5rem;
  margin: 0;
  outline-width: 0ch;
  text-decoration: none;
  background: rgb(65, 123, 158);
  background: var(--main-dark);
  color: #ffffff;
  font-family: inherit;
  font-size: 1.5rem;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, 
              transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.button1:hover,
.button1:focus {
  transform: scale(1.03);
  background: rgb(29, 53, 87);
  background: var(--dark);
  color: white !important;
  text-decoration: none !important;
}

.button1:active {
  transform: scale(0.95);
  color: white !important;
  text-decoration: none !important;
}

button:active {
  transform: scale(0.95);
}

.switchBtn {
  display: inline-block;
  border: none;
  border-radius: 20px;
  padding: .5rem 1rem;
  margin: 0;
  outline-width: 0ch;
  text-decoration: none;
  color: rgb(65, 123, 158);
  color: var(--main-dark);
  background-color: transparent;
  font-family: sans-serif;
  font-size: 1.4rem;
  cursor: pointer;
  text-align: center;
  transition: color 250ms ease-in-out, 
              transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.switchBtn:hover,
.switchBtn:focus {
  color: rgb(0, 0, 0);
  text-decoration: underline;
}

.card {
  padding: 1.2rem 4rem 3rem 4rem;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgba(0,0,0,0.25);
  margin: 3rem 0rem;
  background-color: white;
}

.extraSmall{
  font-size: 1.2rem;
  font-weight: bold;
  font-family: Montserrat, sans-serif;
}

.emptySpace{
  text-align: center;
  padding-top: 8rem;
}

.flexContainer{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap-reverse;
  align-items: baseline;
  margin: 0;
}

@media (max-width: 300px) {
  html{
    font-size: 8px;
  }

  #mission{
    display: none;
  }

  .button1{
    margin-top: 2rem;
  }
}

@media (max-width: 400px){
  .container{
    padding: 10px;
  }
}

@media (max-width: 510px) {
  .flexContainer{
    display: block;
  }

  #firstI{
    width:auto;
    max-width: 100%;
  }

  #secondI{
    width: auto;
    max-width: 100%;
    padding: 1rem 1.5rem;
  }

  .input{
    max-width: 100%;
  }
}

.flexItem{
  width:auto;
  max-width: 50%;
}

.smallBox{
  background-color: rgb(29, 53, 87);
  background-color: var(--dark);
  padding: 0rem 2rem;
  border-radius: 10px;
  margin-top: 2rem;
  width: auto;
  max-width: 35%;
  height: 50%;
  color: white;
  box-shadow: 0px 0px 50px 20px rgb(241, 250, 238);
  box-shadow: 0px 0px 50px 20px var(--light);
  font-family: Montserrat, sans-serif;
}
.containerFull{
    margin: 0px;
    padding: 0px;
}

#title{
    color: var(--main-dark);
    font-size: 7rem;
    width: 65%;
    padding: 5%;
    margin: 2rem auto -5rem auto;
    text-align: center;
}

h2{
    color: var(--main-dark);
    font-size: 4rem;
    width: 65%;
    padding: 5%;
    margin: auto;
}

@media (max-width: 300px) {
    #title{
        font-size: 4.4rem;
    }
}

@media (max-width: 500px) {
    #title{
        font-size: 5rem;
    }
}

@media (max-width: 600px) {
    #flexBoxFrontPage{
        margin: 0px;
        width: 100%;
        flex-direction: column;
        justify-content: center;
    }

    .button2{
        margin: 2rem;
    }

    .containerFull{
        padding: 3rem;
    }
}

.flexBox{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 50%;
    margin: auto;
}

.highlight{
    color: var(--highlight);
}

.button2{
    background-color: var(--main-light);
    font-size: 2.5rem;
    border-radius: 30px;
    padding: 1.3rem 2.5rem;
    margin: 0rem 2rem;
    min-width: 160px;
    transform: scale(.95);
}

.button2:hover, .button2:focus{
    background-color: var(--main-light);
    border: 0px solid white;
    transform: scale(0.98);
}

.button2:active{
    background-color: var(--main-dark);
}

h4{
    font-size: 2.5rem;
    color: var(--dark);
    text-align: center;
    margin: 3rem auto;
    font-weight: 400;
}

#catBox{
    margin: auto;
}
  
.catBtn{
    background-color: var(--main-light);
    margin: .5rem .5rem;
}

h1{
    margin-top: 3.5rem;
}
.form {
    display: grid;
}

.label {
    font-size: 1.2rem;
    margin-bottom: 0.2rem;
    margin-left: 2rem;
    text-transform: uppercase;
    font-family: Montserrat, sans-serif;
    font-weight: bold;
}

.input {
    font-size: 1.6rem;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    padding: 1rem 2rem;
    outline-width: 0ch;
    border-radius: 20px;
    background-color: var(--dark);
    color: white;
    border: 1px solid #ddd;
    margin-bottom: 1rem;
}

::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(224, 224, 224);
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(224, 224, 224);
    opacity: 1; /* Firefox */
}

::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(224, 224, 224);
    opacity: 1; /* Firefox */
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(224, 224, 224);
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color:  rgb(224, 224, 224);
}

::-ms-input-placeholder { /* Microsoft Edge */
    color:  rgb(224, 224, 224);
}
